.client-card {
    box-shadow: 0 2px 1px -1px rgba(0, 0, 0, .2), 0 1px 1px 0 rgba(0, 0, 0, .14), 0 1px 3px 0 rgba(0, 0, 0, .12);
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 6px solid rgb(97, 205, 187);
}

.attributes {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px;
}

.attributes > * {
    margin: 6px 0;
}